import { useContext, useEffect, useState } from "react";
import { Context } from "../../context";

const SearchBar = ({ searchTerm, setSearchTerm, setIsLoading }) => {
  const { openSidebar, searchResult } = useContext(Context);
  const [searchResultValue, setSearchResultValue] = searchResult;

  useEffect(() => {
    setSearchResultValue([]);
  }, []);

  const handleChange = (e) => {
    setSearchTerm(e.target.value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setSearchResultValue([]);
    setIsLoading(true);

    // fetch from omdbapi by search term
    fetch(`https://www.omdbapi.com/?s=${searchTerm}&apikey=40d1fefd`)
      .then((res) => res.json())
      .then((data) => {
        // collect all imdbIDs from the response
        // since the response doesn't contain ratings information, a 2nd fetch by each individual imdbID is needed.
        let ids = data.Search.map((item) => item.imdbID);

        const allPromises = ids.map(async (id) => {
          const res = await fetch(
            `https://www.omdbapi.com/?i=${id}&apikey=40d1fefd`
          );
          return await res.json();
        });
        Promise.all(allPromises).then((data) => {
          setSearchResultValue(data);
          setIsLoading(false);
        });
      })
      .catch((err) => console.log(err));
  };

  return (
    // Search Bar
    <div className="container relative flex flex-col mt-5">
      <div className="container flex">
        <form onSubmit={handleSubmit}>
          <input
            className="w-80 bg-white h-10 px-5 pr-16 filter drop-shadow-white-sm rounded-full text-sm transform focus:outline-none focus:scale-105 duration-200 focus:border-opacity-100 border-2 border-yellow-400 border-opacity-0 hover:border-opacity-100"
            type="text"
            name="searchTerm"
            placeholder="Search..."
            value={searchTerm}
            onChange={handleChange}
            autoComplete="off"
          />
          <button type="submit" className="absolute top-3 right-0 mr-4">
            <svg
              className="text-gray-600 h-4 w-4 fill-current"
              xmlns="https://www.w3.org/2000/svg"
              xmlnsXlink="https://www.w3.org/1999/xlink"
              version="1.1"
              id="Capa_1"
              x="0px"
              y="0px"
              viewBox="0 0 56.966 56.966"
              style={{ enableBackground: "new 0 0 56.966 56.966" }}
              xmlSpace="preserve"
              width="512px"
              height="512px"
            >
              <path d="M55.146,51.887L41.588,37.786c3.486-4.144,5.396-9.358,5.396-14.786c0-12.682-10.318-23-23-23s-23,10.318-23,23  s10.318,23,23,23c4.761,0,9.298-1.436,13.177-4.162l13.661,14.208c0.571,0.593,1.339,0.92,2.162,0.92  c0.779,0,1.518-0.297,2.079-0.837C56.255,54.982,56.293,53.08,55.146,51.887z M23.984,6c9.374,0,17,7.626,17,17s-7.626,17-17,17  s-17-7.626-17-17S14.61,6,23.984,6z" />
            </svg>
          </button>
        </form>
      </div>
    </div>
  );
};

export default SearchBar;
